/* Button used to open the contact form - fixed at the bottom of the page */
.open-button {
  background-color: #555;
  color: white;

  border: none;
  cursor: pointer;
  opacity: 0.8;
  height: 50px;

  border: none;
  outline: 0;
  display: inline-block;
  padding: 8px;
  color: white;
  text-align: center;
  cursor: pointer;
  width: 100%;
  font-size: 18px;
  bottom: 0;
  margin-bottom: 0%;

}

/* The popup form - hidden by default */
.form-popup_regular {
  display: none;
